import { Plugin, CangjieClipboardEvent, Controller } from '@ali/4ever-cangjie';

export default function createOnCangjieCopy(onCangjieCopy?: Plugin['onCangjieCopy']) {
  return (
    event: CangjieClipboardEvent,
    controller: Controller,
    next: () => unknown,
  ): unknown => {
    if (onCangjieCopy) return onCangjieCopy(event, controller, next);
    return next();
  };
}
